<template>
    <div>
        <div>
            <el-row>
                <el-button v-for="item in wetGarbage" :key="item" type="success" plain @click="handleDialog(1)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in recyclableTrash" :key="item" type="primary" plain @click="handleDialog(2)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in HarmfulGarbage" :key="item" type="danger" plain @click="handleDialog(3)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in DryGarbage" :key="item" type="info" plain @click="handleDialog(4)">{{item}}
                </el-button>
            </el-row>
        </div>
        <el-form :inline="true" :model="query" class="query-form" size="mini">
            <el-form-item class="query-form-item">
                <el-input v-model="query.keywords" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" icon="search" @click="onSubmit"></el-button>
                </el-button-group>
            </el-form-item>
        </el-form>
    </div>

</template>

<script>
    import { search } from "../../api/garbage/garbage";

    export default {
        name: 'garbage_index',
        data() {
            return {
                query: {
                    keywords: '' // 
                },
                loading: true,
                result: {
                    cat_title: '', //
                    cat_description: '' // 
                }, // 
                title: '',
                description: '',
                wetGarbage: ['', '', '', ''], // 
                recyclableTrash: ['', '', '', ''], // 
                HarmfulGarbage: ['', '', '', ''], // 
                DryGarbage: ['', '', '', ''] // 
            };
        },
        components: {},
        methods: {
            handleDialog(val) {
                switch (val) {
                    case 1:
                        this.title = ''
                        this.description = ''
                        break;
                    case 2:
                        this.title = ''
                        this.description = ''
                        break;
                    case 3:
                        this.title = ''
                        this.description = ''
                        break;
                    case 4:
                        this.title = ''
                        this.description = ''
                        break;
                }
                this.open()
            },
            // 
            open() {
                this.$alert(this.description, this.title, {
                    confirmButtonText: '',
                    /*callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }*/
                });
            },
            // 
            onSubmit() {
                this.loading = true;
                search(this.query)
                    .then(response => {
                        this.result = response
                        if (this.result.success === 1) {
                            this.loading = false;
                            this.title = this.result.cat_title
                            this.description = this.result.cat_description
                            this.open()
                        } else {
                            this.$message.error(response.msg);
                        }

                    })
                    .catch(() => {
                        this.loading = false;
                        this.result = {};
                    });
            },
        },
        filters: {},
        mounted() {
            document.body.ondrop = function (event) {
                event.preventDefault();
                event.stopPropagation();
            };
        },
        created() {

        }
    };
</script>

<style type="text/scss" lang="scss" scoped>
    .search-btn-next {
        margin-top: 10px;
    }
</style>
